body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main {
  background-color: #fff;
  display: grid;
  grid-template-areas: 
          "title"
          "overview"
          "fridge"
          "recipes"
          "footer";
  font-size: calc(10px + 2vmin);
  color: white;
  margin: 2em;
  text-align: center;
}
.Overview {grid-area: overview;}
.Title {grid-area: title;}
.Fridge {grid-area: fridge;}
.Footer {grid-area: footer;}
.RecipeElement {
  display: grid;
  grid-area: recipes;
  grid-gap: 1em;
  grid-template-columns: 40% auto;
  grid-template-areas:
    "r_title  r_title"
    "display  recipe";
}
.RecipeTitle {grid-area: r_title;}
.RecipeDisplay {grid-area: display;}
.Recipe {grid-area: recipe;}
@font-face {
    font-family: "Staatliches";
    src: url(/whfah/static/media/Staatliches-Regular.db52bdd3.ttf);
}
@font-face {
    font-family: "Adlinnaka";
    src: url(/whfah/static/media/Adlinnaka-BoldDemo.ac5543cf.ttf);
}
@font-face {
    font-family: "Open Sans";
    src: url(/whfah/static/media/OpenSans-Regular.5a798cda.ttf);
}
@font-face {
    font-family: "Open Sans Bold";
    src: url(/whfah/static/media/OpenSans-Bold.0062c346.ttf);
}
@font-face {
    font-family: "Satisfy";
    src: url(/whfah/static/media/Satisfy-Regular.78b50d8f.ttf);
}
@font-face {
    font-family: "Cookie";
    src: url(/whfah/static/media/Cookie-Regular.a57bf31d.ttf)
}
div {
    color: #000;
    grid-area: fridge;
}
li img {
    width: 30px;
    height: 30px;
}
form {
    display: grid;
    grid-template-columns: 33% auto auto;
    grid-template-areas: 
        "first      second      third"
        "buttons    buttons     buttons";
}
button {
    background-color: #87bc4a;
    font-size: 1em;
    border: none;
    color: #fff;
    font-family: "Adlinnaka";
    width: 30%;
    padding: 0.5em;
    margin: 1em;
    transition: background-color 0.5s;
}
button:hover {
    background-color: #454545;
}
.ingredients {
    list-style: none;
    font-family: "Open Sans";
    text-align: left;
}
.ingredients:nth-child(1) {grid-area: first;}
.ingredients:nth-child(2) {grid-area: second;}
.ingredients:nth-child(3) {grid-area: third;}
button[type="submit"] {grid-area: submit;}
button[type="reset"] {grid-area: reset;}
.Buttons {grid-area: buttons; text-align: left;}

.ingredients li label {
    margin-left: 0.5em;
    display: inline;
}
.ingredients li input[type="checkbox"] {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: solid 3px #454545;
    border-radius: 50%;
    padding: 1em;
    transition: background-color 0.5s;
}

.ingredients li input[type="checkbox"]:active, 
.ingredients li input[type="checkbox"]:checked:active {
	box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px 1px 3px rgba(0,0,0,0.1);
}
.ingredients li input[type="checkbox"]:hover {
    background-color: #454545;
}
.ingredients li input[type="checkbox"]:checked {
    background-color: #87bc4a;
    border-color: #87bc4a;
	box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px -15px 10px -12px rgba(0,0,0,0.05), inset 15px 10px -12px rgba(255,255,255,0.1);
	color: #99a1a7;
}
.ingredients li input[type="checkbox"]:checked:after {
	content: '\2611';
	font-size: 14px;
	position: absolute;
	top: 0px;
	left: 3px;
	color: #99a1a7;
}

.RecipeDisplay {
    background-color: #959595;
    margin: -1em;
    margin-left: -2em;
    padding: 0 2em 0 2em;
}
.Recipe {
    background-color: #dfdfdf;
    margin: -1em -2em 0 0;
    padding: 1em 2em 0 2em;
}
.RecipeDisplay, .Recipe {
    padding-bottom: 5em;
}
.Overview {
    height: 20em;
    background-image: url(/whfah/static/media/fridge.81bf90c9.jpg);
    background-size: cover;
    margin: 0 -2em 0 -2em;
    color: #fff;
}
.ov {
    background-color: rgba(255, 255, 255, 0.7);
    margin: 1em;
}
.Source {
    text-align: left;
    background-color: #656565;
    margin: 0 -2em 1em -2em;
    padding: 0.5em 0.5em 0.5em 1em;
}
.Source a {
    color: #efefef;
    font-family: "Satisfy";
    transition: color 0.5s;
}
.Source a:hover {
    color: #959595;
}
.Ingredients {
    background-color: #87bc4a;
    text-align: left;
    padding: 0.5em 2em 2em 2em;
}
.Ingredients li {
    list-style: none !important;
    font-size: 24px;
    background-color: rgba(255,255,255, 0.5);
    margin: 1em -0.5em 1em -2em;
    padding: 0.5em;
    border-radius: 20px;
    transition: background-color, 0.5s;
}
.Ingredients li:hover {
    background-color: rgba(255,255,255,0.8);
}
